        <h1>Temps coureurs</h1>
        <div class="container">
          <table>
            <thead>
              <tr>
                <td>Temps</td>
                <td>Dossard</td>
                <td>Distance</td>
              </tr>
            </thead>
            <TransitionGroup name="listTemps" tag="tbody">
              <tr v-for="elt in temps" :key="elt.timestamp">
                <td>{{ duree(elt.timestamp) }}</td>
                <td>{{elt.dossard}}</td>
                <td>{{elt.distance}}km</td>
              </tr>
            </TransitionGroup >
          </table>
        </div>
