<script>
import axios from 'axios'
import io from 'socket.io-client';

export default {
  name: "DashboardComponent",
  props: ['course'],
  data: function () {
    return {
      nav: "classement",
      nDossard: "",
      temps: [],
      photosFiltre: [],
      socket : io('localhost:3000'),
      photos: [],
      classements: [],
    };
  },
  watch: {
    nDossard(){
      this.photosFiltre = this.photos.filter((photo) => {
        if(photo.dossard == this.nDossard || this.nDossard == ''){
          return true
        }else{
          return false
        }
      })
    },
    course(){
      this.getTemps()
    }
  },
  created() {
    this.getTemps()
    this.getClassement()
    this.getPhotos()
    this.socket.on('course', (data) => {
      if(data == this.course.id){
        this.getTemps()
        this.getClassement()
        this.getPhotos()
      }
    });
  },
  methods: {
    duree(time){
      let diff = time - this.course.timestamp
      let h = Math.floor(diff / 3600)
      diff = diff - (h * 3600)
      let m = Math.floor(diff / 60)
      diff = diff - (m * 60)

      if(h < 10){
        h = "0" + h
      }

      if(m < 10){
        m = "0" + m
      }

      if(diff < 10){
        diff = "0" + diff
      }

      return h + ":" + m + ":" + diff
    },
    getTemps(){
      axios.get('http://localhost:9000/pointControle/' + this.course.id)
      .then((response) => {
        this.temps = response.data
      })
      .catch((error) => {
        console.log(error)
      })
    },  
    getClassement(){
      axios.get('http://localhost:9000/pointControle/classement/' + this.course.id)
      .then((response) => {
        this.classements = response.data
      })
      .catch((error) => {
        console.log(error)
      })
    },
    getPhotos(){
      axios.get('http://localhost:9000/pointControle/photos/' + this.course.id)
      .then((response) => {
        this.photos = response.data
        this.photosFiltre = this.photos
      })
      .catch((error) => {
        console.log(error)
      })
    },
    navigate(str) {
      this.nav = str;
    },
  },
};
</script>

