<template>
    <div id="accueil-app">
        <div id="diapo"></div>
        <div id="filtre"></div>
        <div id="course-container">
            <div>
                <h1>Course</h1>
                <span>Indiquer le nom de la course</span>
            </div>
            <select name="course" id="course-select" v-model="courseSelect">
                <option value="">Sélectionner la course</option>
                <option v-for="option in options" :key="option.id" :value="option.id">{{ option.nom }}</option>
            </select>
            <button @click="valid">Valider</button>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'AccueilComponent',
    data: function () {
        return {
            courseSelect: "",
            options: []
        }
    },
    created() {
        axios.get("http://localhost:9000/course/")
        .then((response) => {
            this.options = response.data
        })
    },
    methods: {
        valid: function(){
            this.options.forEach((elt) => {
                if(elt.id == this.courseSelect){
                    this.$emit('submit-course',elt)
                }
            })
        }
    }
}
</script>

