<template>
  <transition>
    <AccueilComponent @submit-course="submitCourse" v-if="course == null"/>
    <DashboardComponent :course="course" v-else/>
  </transition>
</template>

<script>
import AccueilComponent from './components/Accueil.vue'
import DashboardComponent from './components/Dashboard.vue'

export default {
  name: 'App',
  components: {
    AccueilComponent,
    DashboardComponent
  },
  data: function () {
        return {
          course: null,
        }
    },
  methods: {
      submitCourse(elt) {
        this.course = elt
    }
  }
}
</script>

